// Simulation d'une base de donnes utilisateurs pour distinguer login vs signup
const USERS_DB_KEY = 'heresse_users_db';

// Utilisateurs existants simuls par dfaut
const defaultUsers = [
  {
    id: '1',
    email: 'user@example.com',
    phone: '+33612345678',
    password: 'password123',
    kycComplete: true
  },
  {
    id: '2', 
    email: 'test@test.com',
    phone: '+33687654321',
    password: 'test123',
    kycComplete: true
  }
];

// Initialiser la base de donnes avec des utilisateurs par dfaut
export const initializeUserDatabase = () => {
  const existingDB = localStorage.getItem(USERS_DB_KEY);
  if (!existingDB) {
    localStorage.setItem(USERS_DB_KEY, JSON.stringify(defaultUsers));
  }
};

// Rcuprer tous les utilisateurs
export const getAllUsers = () => {
  const usersDB = localStorage.getItem(USERS_DB_KEY);
  return usersDB ? JSON.parse(usersDB) : [];
};

// Vrifier si un utilisateur existe par email
export const checkUserExistsByEmail = (email) => {
  const users = getAllUsers();
  return users.find(user => user.email.toLowerCase() === email.toLowerCase());
};

// Vrifier si un utilisateur existe par tlphone
export const checkUserExistsByPhone = (phone) => {
  const users = getAllUsers();
  return users.find(user => user.phone === phone);
};

// Authentifier un utilisateur avec email/mot de passe
export const authenticateUser = (email, password) => {
  const user = checkUserExistsByEmail(email);
  if (user && user.password === password) {
    return { success: true, user };
  }
  return { success: false, error: 'Email ou mot de passe incorrect' };
};

// Authentifier un utilisateur avec tlphone (simulation SMS)
export const authenticateUserBySMS = (phone) => {
  const user = checkUserExistsByPhone(phone);
  if (user) {
    return { success: true, user };
  }
  return { success: false, error: 'Numro de tlphone non reconnu' };
};

// Crer un nouvel utilisateur
export const createUser = (userData) => {
  const users = getAllUsers();
  
  // Vrifier si l'utilisateur existe dj
  const existingUser = checkUserExistsByEmail(userData.email) || 
                      (userData.phone && checkUserExistsByPhone(userData.phone));
  
  if (existingUser) {
    return { success: false, error: 'Un compte existe dj avec ces informations' };
  }
  
  const newUser = {
    id: Date.now().toString(),
    ...userData,
    kycComplete: false // Les nouveaux utilisateurs doivent faire le KYC
  };
  
  users.push(newUser);
  localStorage.setItem(USERS_DB_KEY, JSON.stringify(users));
  
  return { success: true, user: newUser };
};

// Mettre  jour le statut KYC d'un utilisateur
export const updateUserKycStatus = (userId, kycComplete = true) => {
  const users = getAllUsers();
  const userIndex = users.findIndex(user => user.id === userId);
  
  if (userIndex !== -1) {
    users[userIndex].kycComplete = kycComplete;
    localStorage.setItem(USERS_DB_KEY, JSON.stringify(users));
    return { success: true, user: users[userIndex] };
  }
  
  return { success: false, error: 'Utilisateur non trouv' };
};

// Rcuprer un utilisateur par ID
export const getUserById = (userId) => {
  const users = getAllUsers();
  return users.find(user => user.id === userId);
};
